
























import Vue from 'vue';
import {Component, Prop} from 'vue-property-decorator';


@Component
export default class Numberpad extends Vue {
  @Prop(String) readonly value!: string;
  output = this.value;

  inputEvent(e: MouseEvent) {
    const btn = (e.currentTarget as HTMLButtonElement);
    const inputContent = btn.textContent!;
    if (this.output === '0') {
      if (inputContent === '0') {
        return;
      }
      if ('123456789'.indexOf(inputContent) >= 0) {
        this.output = inputContent;
        return;
      }

    }
    if (this.output.indexOf('.') >= 0 || this.output.length === 0) {
      if (inputContent === '.') return;
    }
    this.output += inputContent;

  }

  deleteContent() {
    if (this.output.length >= 1) {

      this.output = this.output.slice(0, -1);
    }

  }

  clearContent() {
    this.output = '0';
  }

  onOk() {
    if(this.output==='' || this.output==='0') return window.alert('金额不能为空');
    this.$emit('update:value',this.output);
    this.$emit('submit');
    this.output = '0';

  }

}
